html {
	font-family: $main-font, sans-serif;
	font-size: convert-to-percentage-font($font-size);
	color: $primary-color;

	background-color: $background-color;
}

body {
	height: 100%;
}

.content {
	overflow: hidden;
}

.container {
	@include media-down(md) {
		padding-right: 10px;
		padding-left: 10px;
	}
	@include media-down(xs) {
		max-width: rem(320);
		padding: 0;
	}
	max-width: rem($container-with-gap-width);
	margin: 0 auto;
	padding: 0 rem($gap-width);
}

.disable-scroll {
	position: fixed;
	top: 0;
	left: 0;

	overflow: hidden;
	overscroll-behavior: none;

	width: 100%;
	height: 100vh;
}

::-webkit-scrollbar-thumb {
	background: linear-gradient(179.4deg, #4031ee -25.42%, #009ef8 110.9%);
	border-radius: 10px;
}

::-webkit-scrollbar {
	width: 5px;
	height: 5px;

	background: #01061b;
}

.visually-hidden {
	position: absolute;

	overflow: hidden;

	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;

	clip: rect(0 0 0 0);
	border: 0;
}
